import React, { Component }  from "react";
import { connect } from "react-redux";
import Select from 'react-select';
import AddOnFilter from "./addon-filter.component";
import CustomButton from '../custom-button/custom-button.component';

import './condition.styles.scss';

import { addChart, resetChart } from '../../redux/condition/condition.action';
import { lineOptions, processOptions, lotOptions, stepOptions } from './condition.data';



class Conditions extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectLineValue: [{value:'L01'}], selectLotValue: [{value:'LOT01'}], selectProcessValue: [{value:'P01'}], selectStepseqValue: [{value:'1'}],
      recordCount: 0,
      selectedValues:{"k_from":"01/01/19", "k_to":"12/31/19", "k_where":"ITEM_VALUE > 0"}, //: this.props.origSelectedValues
      interval: null,
    };

    this.btnResetChart_click = this.btnResetChart_click.bind(this);
  }
  
  btnResetChart_click = () => {
    const { gpudb } = this.props;
    const tableName = '__SAMSUNG_TMP';

    gpudb.clear_table( tableName, '', {} ).then( resp => {
      console.log("clear_table :" + JSON.stringify(resp));
    });
    this.props.resetChart();
    alert("   ");
  }  

  btnGenChart_click = () => {
    console.log(this.state.selectLineValue);
    let chartsArray =[];
    // var charts= [{'viewName':'', 'condition':''}];
    var lines     = this.state.selectLineValue;
    var processes = this.state.selectProcessValue;
    var lots      = this.state.selectLotValue;
    var steps     = this.state.selectStepseqValue;

    chartsArray = (lines !==null && lines.length > 0) ? this.mapArray( lines,     chartsArray, 'LINE' ) : chartsArray
    chartsArray = (processes !==null && processes.length > 0) ? this.mapArray( processes, chartsArray, 'PROCESS' ) : chartsArray
    chartsArray = (lots !==null && lots.length > 0) ? this.mapArray( lots,      chartsArray, 'LOT_ID' ) : chartsArray
    chartsArray = (steps !==null && steps.length > 0) ? this.mapArray( steps,     chartsArray, 'STEP_SEQ' ) : chartsArray  

    chartsArray.forEach( chart  => {
      let chartElement = {};
      chartElement.viewName  = chart.viewName;
      chartElement.condition = chart.condition;

      this.props.addChart(chartElement);
    })  
  };
  
  mapArray = ( cond, charts, colName ) => {
    let chartsArr = [];
    let chart;
    let idx = 0;
    const viewPrefix = 'ChartExample_view_';

    if (cond.length !== 0 &&  charts.length > 0) {
      cond.forEach( (item ) => {
        charts.forEach( (prevItem ) => {
          chart = {};
          chart.viewName = prevItem.viewName + item.value;
          chart.condition =  prevItem.condition + ' AND ' + colName + "='" + item.value + "'";

          chartsArr[idx++] = chart;
        })
      });
    } else if (cond.length !== 0 && charts.length === 0){
        cond.forEach( (item) => {
          chart = {};
          chart.viewName = viewPrefix + item.value;
          chart.condition = ' ' + colName + "='" + item.value + "' ";

          chartsArr[idx++] = chart;
        })
    };      
    return chartsArr; 
  }

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleChangeLine = line => {
    this.setState( {selectLineValue: line} );
  }  

  handleChangeProcess = proc => {
    this.setState( {selectProcessValue: proc} );
  }  

  handleChangeStep = step => {
    this.setState( {selectStepseqValue: step} );
  }  

  handleChangeLot = lot => {
    this.setState( {selectLotValue: lot} );
  }  

  render() {
    return (
      <div className={this.props.hidden ? 'condition-hidden': 'condition'}>
        <div className='options'>
          <div className='items'>
            <div className='item'>
              <span className='title'>Line</span>
              <Select
                closeMenuOnSelect={false}
                isMulti
                defaultValue={[lineOptions[0]]}
                options={lineOptions}
                onChange={this.handleChangeLine}
              />
            </div>
            <div className='item'>
              <span className='title'>Process</span>
              <Select
                closeMenuOnSelect={false}
                isMulti
                defaultValue={[processOptions[0]]}
                options={processOptions}
                onChange={this.handleChangeProcess}
              />
            </div>
            <div className='item'>
              <span className='title'>Lot</span>
              <Select
                closeMenuOnSelect={false}
                isMulti
                defaultValue={[lotOptions[0]]}
                options={lotOptions}
                onChange={this.handleChangeLot}
              />
            </div>
            <div className='item'>   
              <span className='title'>Step Sequence</span>       
              <Select
                closeMenuOnSelect={false}
                isMulti
                defaultValue={[stepOptions[0]]}
                options={stepOptions}
                onChange={this.handleChangeStep}
              />
            </div> 
          </div>

            <div className='buttons'>
              <CustomButton onClick={this.btnGenChart_click}> Generate Chart </CustomButton>
              <CustomButton onClick={this.btnResetChart_click}> Reset </CustomButton>
            </div>  
        </div>  
        <div className='addon'>  
          <AddOnFilter />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return { hidden: state.condition.hidden 
  };
};

function mapDispatchToProps(dispatch) {
  return {
    addChart : chart => dispatch( addChart( chart )),
    resetChart : () => dispatch( resetChart() ),
  };
}

export default connect( mapStateToProps, mapDispatchToProps )( Conditions );