export const MAX_TRUNCATE_CHAR = 100;
export const IMAGE_MARKER_BYTES = {
  jpg: [255, 216],
  png: [137, 80, 78, 71, 13, 10, 26, 10],
};

export const stringToBytes = str => {
  let ch;
  let st;
  const re = [];
  let j = 0;
  if (str && str !== '') {
    for (let i = 0; i < str.length; i++) {
      ch = str.charCodeAt(i);
      if (ch < 127) {
        re[j++] = ch & 0xff;
      } else {
        st = []; // clear stack
        do {
          st.push(ch & 0xff); // push byte to stack
          ch = ch >> 8; // shift value down by 1 byte
        } while (ch);
        // add stack contents to result
        // done because chars have "wrong" endianness
        st = st.reverse();
        for (let k = 0; k < st.length; ++k) re[j++] = st[k];
      }
    }
  }
  return re;
};

const CheckImage = str => {
  // Get byte array of input
  const byteArray = stringToBytes(str.substring(0, MAX_TRUNCATE_CHAR));

  // Check for potential match
  let jpgMatch = true;
  IMAGE_MARKER_BYTES.jpg.forEach((byte, index) => {
    if (
      !byteArray[index] ||
      IMAGE_MARKER_BYTES.jpg[index] !== byteArray[index]
    ) {
      jpgMatch = false;
    }
  });
  if (jpgMatch) {
    return {
      contentType: 'image/jpg',
      base64String: btoa(str),
    };
  }

  let pngMatch = true;
  IMAGE_MARKER_BYTES.png.forEach((byte, index) => {
    if (
      !byteArray[index] ||
      IMAGE_MARKER_BYTES.png[index] !== byteArray[index]
    ) {
      pngMatch = false;
    }
  });
  if (pngMatch) {
    return {
      contentType: 'image/png',
      base64String: btoa(str),
    };
  }

  return null;
};

export default CheckImage;