import React, {Component}  from 'react';
import { connect } from "react-redux";
import DatePicker from "react-datepicker";
import { format } from 'date-fns';
import FormInput from '../form-input/form-input.component';
import "react-datepicker/dist/react-datepicker.css";

import { updateFilterWhere, updateFilterFrom, updateFilterTo, } from '../../redux/condition/condition.action';
import './addon-filter.styles.scss';

class AddOnFilter extends Component {
  state = {
    startDate: new Date('2019-10-01 00:00:00'),
    endDate: new Date('2019-12-31 23:59:59'),
    wFrom: '2019-10-01 00:00:00',
    wTo: '2019-12-31 23:59:59',
    whereClause: "", // "ITEM_VALUE > 0",
    selectedValues:{"k_from":"01/01/19", "k_to":"12/31/19", "k_where":"ITEM_VALUE > 0"},
  };

  handleChangeStartDate = date => {
    this.setState({
      startDate: date     // '2019-10-01 00:00:00' //   // wFrom: moment(date).format("YYYY-MM-DD HH:mm:ss")
    });
    this.props.updateFilterFrom( format(date, 'yyyy-MM-dd HH:mm:ss') );  
  };

  handleChangeEndDate = date => { 
    this.setState({
      endDate: date       // '2019-12-31 00:00:00'  // 
    });
    this.props.updateFilterTo( format(date, 'yyyy-MM-dd HH:mm:ss') );  
  };

  handleChange = event => {
    const { name, value } = event.target;

    this.setState({ [name]: value });
    this.props.updateFilterWhere(value); 
  };

  render () {
    const { whereClause } = this.state;

    return (
      <div className='addon'>
        <span>FROM</span>
        <DatePicker className='dateSelect'
          selected={this.state.startDate}
          onChange={this.handleChangeStartDate}
          showTimeSelect
          dateFormat="yyyy-MM-dd hh:mm:ss"
          placeholderText="Click to select a date"
        />
        <span>TO</span>
        <DatePicker className='dateSelect'
          selected={this.state.endDate}
          onChange={this.handleChangeEndDate}
          showTimeSelect
          dateFormat="yyyy-MM-dd hh:mm:ss"
          placeholderText="Click to select a date"
        />

        <div className='input'>
          <FormInput
            type='text'
            name='whereClause'
            value={whereClause}
            onChange={this.handleChange}
            label='WHERE'
            required
          />
        </div>
      </div>
    )
  }
}  

function mapDispatchToProps(dispatch) {
  return {
    updateFilterWhere  : filterWhere  => dispatch( updateFilterWhere( filterWhere )),
    updateFilterFrom   : filterFrom   => dispatch( updateFilterFrom( filterFrom )),
    updateFilterTo     : filterTo     => dispatch( updateFilterTo( filterTo )),
  };
}

export default connect( null, mapDispatchToProps )( AddOnFilter );

