import React, { Component } from 'react';
import DrawLayer from './DrawLayer';

const BUTTON_STYLE = {
  borderRadius: '18px',
  backgroundColor: '#eee',
  padding: '5px',
  opacity: '0.6',
  float: 'left',
  marginBottom: '5px',
};

const SELECTED_BUTTON_STYLE = {
  borderRadius: '18px',
  backgroundColor: '#ddd',
  padding: '2px',
  opacity: '0.8',
  float: 'left',
  marginBottom: '5px',
  border: '1px solid #ccc',
};

class Toolbar extends Component {
  constructor(props) {
    super(props);

    this.onCircleClick = this.onCircleClick.bind(this);
    this.onRectangleClick = this.onRectangleClick.bind(this);
    this.onPolygonClick = this.onPolygonClick.bind(this);
    this.onFreehandClick = this.onFreehandClick.bind(this);
    this.onClearClick = this.onClearClick.bind(this);
    this.onInfoClick = this.onInfoClick.bind(this);
  }

  // NOTE: Unused.
  onCircleClick(e) {
    e.preventDefault();
    const { drawMode, onCircle, onCancel } = this.props;
    if (onCircle) {
      onCircle();
    }
  }

  onRectangleClick(e) {
    e.preventDefault();
    const { drawMode, onRectangle, onCancel } = this.props;
    if (drawMode !== DrawLayer.DrawMode.Rectangle) {
      if (onRectangle) {
        onRectangle();
      }
    } else {
      if (onCancel) {
        onCancel();
      }
    }
  }

  onPolygonClick(e) {
    e.preventDefault();
    const { drawMode, onPolygon, onCancel } = this.props;
    if (drawMode !== DrawLayer.DrawMode.Polygon) {
      if (onPolygon) {
        onPolygon();
      }
    } else {
      if (onCancel) {
        onCancel();
      }
    }
  }

  onFreehandClick(e) {
    e.preventDefault();
    const { drawMode, onFreehand, onCancel } = this.props;
    if (drawMode !== DrawLayer.DrawMode.Freehand) {
      if (onFreehand) {
        onFreehand();
      }
    } else {
      if (onCancel) {
        onCancel();
      }
    }
  }

  onClearClick(e) {
    e.preventDefault();
    const { onClear } = this.props;
    if (onClear) {
      onClear();
    }
  }

  onInfoClick(e) {
    e.preventDefault();
    const { popupMode, onInfo, onCancel } = this.props;
    if (popupMode) {
      if (onCancel) {
        onCancel();
      }
    } else {
      if (onInfo) {
        onInfo();
      }
    }
  }

  render() {
    const { top = 0, left = 0, drawMode, popupMode, onAutofit } = this.props;

    return (
      <div
        style={{
          position: 'absolute',
          top: `${top}px`,
          left: `${left}px`,
          width: '28px',
        }}
      >
        <img
          title="Rectangle Selection"
          width="24"
          height="24"
          style={
            drawMode !== DrawLayer.DrawMode.Rectangle
              ? BUTTON_STYLE
              : SELECTED_BUTTON_STYLE
          }
          onClick={this.onRectangleClick}
          // TODO: Add credit link on Reveal page.
          // <a href="https://icons8.com/icon/98384/rectangular">Rectangular icon by Icons8</a>
          src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAAA4SURBVEhLYxgFgwr8pzLGANgUUYIxAE4JEsGoBQTBqAUEwagFBMEIsIBaGANgU0QJHgWDAjAwAAAlxX+BJxmHcwAAAABJRU5ErkJggg=="
        />
        <img
          title="Polygon Selection"
          width="24"
          height="24"
          style={
            drawMode !== DrawLayer.DrawMode.Polygon
              ? BUTTON_STYLE
              : SELECTED_BUTTON_STYLE
          }
          onClick={this.onPolygonClick}
          // TODO: Add credit link on Reveal page.
          // Credit: <a href="https://icons8.com/icon/98435/pentagon">Pentagon icon by Icons8</a>
          src="data:image/png;base64,"
        />
        <img
          title="Freehand Selection"
          width="24"
          height="24"
          style={
            drawMode !== DrawLayer.DrawMode.Freehand
              ? BUTTON_STYLE
              : SELECTED_BUTTON_STYLE
          }
          onClick={this.onFreehandClick}
          // TODO: Add credit link on Reveal page.
          // Credit: <a href="https://icons8.com/icon/84882/information">Information icon by Icons8</a>
          src="data:image/png;base64,"
        />
        <img
          title="Clear Selection"
          width="24"
          height="24"
          style={BUTTON_STYLE}
          onClick={this.onClearClick}
          // TODO: Add credit link on Reveal page.
          // Credit: <a href="https://icons8.com/icon/98131/delete-trash">Delete Trash icon by Icons8</a>
          src="data:image/png;base64,"
        />
        <img
          title="Info Pop-up Tool"
          width="24"
          height="24"
          style={popupMode ? SELECTED_BUTTON_STYLE : BUTTON_STYLE}
          onClick={this.onInfoClick}
          // TODO: Add credit link on Reveal page.
          // Credit: <a href="https://icons8.com/icon/84882/information">Information icon by Icons8</a>
          src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAABtSURBVEhLYxgxgA2Iu4D4KRR3QMWoBtqB+D8aBolRDTwDYnQLQD6hGqC5BTQPIlCEggyERTKITdVIHtrgCBCjhz9IjGrgMBCjWwDCVAejFhAEoxYQBEPXAmw5GYZBmZBigCsng/AhIB7WgIEBAFtQVZEBZCVgAAAAAElFTkSuQmCC"
        />
        <img
          title="Fit to Data"
          width="24"
          height="24"
          style={BUTTON_STYLE}
          onClick={onAutofit}
          // TODO: Add credit link on Reveal page.
          // Credit: <a href="https://icons8.com/icon/87339/target">Target icon by Icons8</a>
          src="data:image/png;base64,"
        />
      </div>
    );
  }
}

export default Toolbar;
