export const addConditionToChart = (charts, condition) => {
  const existingChart = charts.find(
    chart => chart.viewName === condition.viewName
  );

  if (existingChart) {
    return charts.map(chart =>
      chart.viewName === condition.viewName
        ? { ...chart }
        : chart
    );
  }

  return [...charts, { ...condition }];
};

export const updateMinMaxInChart = (charts, action) => {
  return charts.map( (chart, index) => 
    chart.viewName !== action.viewName
      ? chart   // leave unchanged
      : { ...chart, minValue: action.minValue, maxValue: action.maxValue, }
          
  )         
};

export const updateRowCountInChart = (charts, action) => {
  return charts.map( (chart, index) => 
    chart.viewName !== action.viewName
      ? chart   // leave unchanged
      : { ...chart, rowCount: action.rowCount  }
          
  )         
};

