import { createStore, applyMiddleware, compose} from 'redux'; // added compose
import { persistStore } from 'redux-persist';
import logger from 'redux-logger';

import rootReducer from './root-reducer';

const middlewares = [logger];

// added to use redux inspector util -  , window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()

const storeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
export const store = createStore(rootReducer, storeEnhancers(applyMiddleware(...middlewares)) );

export const persistor = persistStore(store);

export default { store, persistStore };
