import ConditionActionTypes from './condition.types';


export const addChart = (payload) => ({
  type: ConditionActionTypes.ADD_CHART, payload 
});
export const updateFilterWhere = (payload) => ({
  type: ConditionActionTypes.UPDATE_FILTER_WHERE, payload
});
export const updateFilterFrom = (payload) => ({
  type: ConditionActionTypes.UPDATE_FILTER_FROM, payload
});
export const updateFilterTo = (payload) => ({
  type: ConditionActionTypes.UPDATE_FILTER_TO, payload
});
export const updateChartMinMax = (payload) => ({
  type: ConditionActionTypes.UPDATE_CHART_MIN_MAX, payload
});
export const updateRowCount = (payload) => ({
  type: ConditionActionTypes.UPDATE_ROW_COUNT, payload
});
export const toggleConditionHidden = () => ({
  type: ConditionActionTypes.TOGGLE_CONDITION_HIDDEN
});

export const resetChart = () => ({
  type: ConditionActionTypes.RESET_CHART
});

export const updateCbPoints = (payload) => ({
  type: ConditionActionTypes.UPDATE_CB_POINITS, payload
});