const conditionReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case ConditionActionTypes.ADD_CHART:
      return {
        ...state,
        charts: addConditionToChart(state.charts, action.payload)
      };
    case ConditionActionTypes.UPDATE_FILTER_WHERE:
      return {
        ...state,
        filterWhere: action.payload
      }; 
    case ConditionActionTypes.UPDATE_FILTER_FROM:
      return {
        ...state,
        filterFrom: action.payload
      };
    case ConditionActionTypes.UPDATE_FILTER_TO:
      return {
        ...state,
        filterTo: action.payload
      }; 
    case ConditionActionTypes.UPDATE_CHART_MIN_MAX:
      return {
        ...state,
        charts: updateMinMaxInChart(state.charts, action.payload)
      };
    case ConditionActionTypes.UPDATE_ROW_COUNT:
      return {
        ...state,
        charts: updateRowCountInChart(state.charts, action.payload)
      };       
    case ConditionActionTypes.TOGGLE_CONDITION_HIDDEN:
      return {
        ...state,
        hidden: !state.hidden
      };
    case ConditionActionTypes.RESET_CHART:
      return {
        ...state,
        charts: []
      };
    case ConditionActionTypes.UPDATE_CB_POINITS:
      return {
        ...state,
        cbPointColors: action.payload
      }; 


    default:
      return state;
  }
};

export default conditionReducer;