import React, { Component } from 'react';
import { connect } from 'react-redux';

import { toggleConditionHidden } from '../../redux/condition/condition.action.js';

import { ReactComponent as UNFOLD_LESS } from '../../assets/unfold_less.svg';
import { ReactComponent as UNFOLD_MORE } from '../../assets/unfold_more.svg';

import './unfold-icon.styles.scss';

class Unfold extends Component {
  render () {
    return (
      <div className='unfold-icon' onClick={this.props.toggleConditionHidden}>
        { this.props.hidden ? <UNFOLD_MORE className='icon' /> 
                            : <UNFOLD_LESS className='icon' /> }
      </div>
    )
  };
}
const mapDispatchToProps = dispatch => ({
  toggleConditionHidden: () => dispatch(toggleConditionHidden())
});

const mapStateToProps = state => {
  return { hidden: state.condition.hidden 
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Unfold);
   