import React, { Component }  from "react";
import { connect } from "react-redux";
import localStorage from 'local-storage';
import RGL, { WidthProvider } from "react-grid-layout";
//  import css -- IMP!!!
import '../../../node_modules/react-grid-layout/css/styles.css';
import '../../../node_modules/react-resizable/css/styles.css';

import ChartCell from './ChartCell';
import Scatter   from "./scatter.component";
import Condition from '../condition/condition.component'
import GPUdb     from '../../API/GPUdb';

import './chart.styles.scss';

const ReactGridLayout = WidthProvider(RGL);
// const gpudb = new GPUdb( "http://121.162.9.85:30012" );   //121.162.9.85:30012
// const gpudb = new GPUdb( "http://192.168.1.91:9191" ); 
const gpudb = new GPUdb( "http://222.98.79.171:9191" ); 

const margin = {
  left: "50",
  right: "5",
  top: "5",
  bottom: "50",
};

const styleOpts = {
  pointsize: ['2'],
  pointshape: ["circle"],
  cb_pointcolors:["PROCESS","P01;P02;P03;P04;P05;P06;P07;P08;P09;P10","FF0000;a83c32;800000;808080;a5c33c;f8ba00;f8cf00;afeeee;00438c;F08080"],   // Legend
  jitter_x: ["0"],
  jitter_y: ["0"],
};

const scatterChart = {
  columnX: "OUT_TIME",
  columnY: "ITEM_VALUE",
  minX: "1570000000000",  //   "01/01/2019",  //1570000000000
  maxX: "1578900000000",  //1578900000000               69847163985 "12/31/2019"
  minY: "-100",
  maxY: "300",
  titleX: "",
  titleY: "",
  formatX: "%m-%d %H:%M",   //  "%m-%d %H:%M"
  formatY: ".3s", 
  width: 800,
  height: 300,
  autoScale: false, 
  autoScaleFactor: "1.00",
  //filterShape: ["circle"],
  popupTemplate: "<div style=\"float:left;\">\r\n<div style=\"font-size:14px;font-weight:bold;\">[ITEM_VALUE]</div>\r\n<strong>Line</strong>: [LINE]<br />\r\n<strong>Process</strong>: [PROCESS]<br />\r\n<strong>Step Seq</strong>: [STEP_SEQ]<br />\r\n<strong>LOT ID</strong>: [LOT_ID]<br />\r\n<strong>WAFER ID</strong>: [WAFER_ID]<br />\r\n<strong>EQP ID</strong>: [EQP_ID]<br />\r\n<strong>Date Time</strong>: [OUT_TIME]<br />\r\n<strong>Value</strong>: [ITEM_VALUE]\r\n</div>",
};  

const filterShape = {
  xArr : [1578900000000, 1570000000000, 1570000000000, 1578900000000],
  yArr : [300, 300, -100, -100],
  x : 0,
  y : 0,
  points : [10,10],
  type : "rectangle",
  width : 0,
  height : 0,
};

const onFit = (scaleX, scaleY) => { };


class Chart extends Component {
  constructor(props) {
    super(props);
    this.state = { layout: [
                              {i: '1', x: 0, y: 0, w: 1, h: 2, minH: 2, maxH: 2},         // *** -- minH & maxH doesnt effect the grid items
                              {i: '2', x: 1, y: 0, w: 1, h: 2, minH: 2, maxH: 2},
                              {i: '3', x: 0, y: 1, w: 1, h: 2, minH: 2, maxH: 2},
                              {i: '4', x: 1, y: 1, w: 1, h: 2, minH: 2, maxH: 2}
                            ],
                  };
    this.onLayoutChange = this.onLayoutChange.bind(this);
  }
  
  componentDidMount() {
    if (this.props.charts.length > 0) {
      this.props.charts.map( (chart, idx) => {
        let layout;
        layout = {i: chart.viewName, x: Math.trunc(idx/2), y: idx%2, w: 1, h: 2, minH: 2, maxH: 2};
        return this.onAddLayout(layout);
      })
    }  
    console.log(this.state.layout);  
  }

  onLayoutChange = (layout) => {
    this.setState({layout});
  }
  
  onResize = (layouts) => {
    this.setState({
      layout: layouts,
    });
  };

  onAddLayout = (layout) => {
    const layouts = [...this.state.layout, layout];
    this.setState({
      layout: layouts,
    });
  };

  render() {
    return ( 
      <div className='condition-box'>
        <Condition gpudb={gpudb} />
        <div className='chart-box'>
            {localStorage.clear()}
            {this.props.charts !== undefined ? this.props.charts.map( (chart, idx) => {
              const onZoom = transform => {
                if (transform) {
                  localStorage.set(
                    "SCATTERPLOT_TRANSFORM_" + chart.viewName,
                    { x: transform.x, y: transform.y, k: transform.k }
                  );
                }
              };
              
              localStorage.set(
                "SCATTERPLOT_TRANSFORM_" + chart.viewName,
                { x: 1.682842677150461, y: 1.9417425381789712, k: 1 }
              );
              
              let lastTransform = localStorage.get(
                "SCATTERPLOT_TRANSFORM_" + chart.viewName
              );
              
              let expression = chart.condition + " AND " + "DATETIME(OUT_TIME) > '" + this.props.filterFrom + "'" + 
                                                " AND " + "DATETIME(OUT_TIME) < '" + this.props.filterTo   + "'" + 
                                                " AND " + this.props.filterWhere; 

              return ( 
        
                  <div className="scatter-main" key={idx}>
                    <div className="chart-item" > 
                      <ChartCell
                        chartDescription={chart.condition}
                        idx={idx}
                        min={chart.minValue}
                        max={chart.maxValue}
                        cnt={chart.rowCount}
                        key={idx}
                      />
                    <Scatter 
                      viewName={chart.viewName} 
                      condition={expression}             //{chart.condition} 
                      parentSelector={"#" + chart.viewName}
                      gpudb={gpudb} 
                      onZoom={onZoom} 
                      lastTransform={lastTransform}
                      table={chart.viewName}
                      columnX={scatterChart.columnX}
                      columnY={scatterChart.columnY}
                      titleX={scatterChart.titleX}
                      titleY={scatterChart.titleY}
                      formatX={scatterChart.formatX}
                      formatY={scatterChart.formatY}
                      minX={scatterChart.minX}
                      maxX={scatterChart.maxX}
                      minY={scatterChart.minY}
                      maxY={scatterChart.maxY}
                      margin={margin}
                      styleOptions={styleOpts}
                      width={scatterChart.width - 20 || 100}
                      height={scatterChart.height - 20 || 100}
                      onFit={onFit}
                      filterShape={filterShape}
                      template={scatterChart.popupTemplate}
                      autoScale={scatterChart.autoScale}
                      autoScaleFactor={scatterChart.autoScaleFactor}
                      //loading={loading}
                      onFilter={false}
                      disableLegend={true}
                      idx={idx}
                      key={chart.viewName}
                    />
                  </div>
                </div>

              )
            }): <div>No Chart</div>}
          
        </div>         
      </div>
    )  
  }
}

Chart.defaultProps = {
  rowHeight: 150,
  cols: 2, // to make grid item 50% or 100%
};

const mapStateToProps = state => {
  return { filterWhere: state.condition.filterWhere, 
           filterFrom:  state.condition.filterFrom, 
           filterTo:    state.condition.filterTo, 
           charts:      state.condition.charts,
  };
};

export default connect( mapStateToProps, null )( Chart ); 

