import React from 'react';
import './ChartCell.styles.scss';

const ChartCell = ({ chartDescription, idx, min, max , cnt, key1 }) => (
    <div className="cell-box" id={key1}>
        <div className="cell-header">
          <h5> : {chartDescription}</h5>
        </div>
        <div className="cell-info">
          <span>Min:{Math.round(min)}</span><br/>
          <span>Max:{Math.round(max)}</span><br/>
          <span>{cnt !== undefined ? 'Count:' + cnt : ''} </span>
        </div>

        <div className="cell-info">
          <div className='DragHandle'>
            <a title="Move Chart" >
              <i className="fa fa-arrows drag"></i>
            </a>
          </div>  
          <a className="remove-chart"
            title="Remove Chart"
            data-toggle="tooltip"
          >
            <i
              className="fa fa-close"
              onClick={() => {

              }}
            />
          </a>
        </div>
    </div>
)

export default ChartCell;
