import React from 'react';
import * as d3 from 'd3';
import { connect } from "react-redux";
import debounce from 'debounce';

import Toolbar from './Toolbar'
import DrawLayer from './DrawLayer';
import Popup from './Popup';
import ClassbreakLegend from './ClassbreakLegend';

import './scatter.styles.css';

import { updateChartMinMax, updateRowCount, } from '../../redux/condition/condition.action';

const ShapeType = DrawLayer.Shape.Type;
const BG_COLOR = '0000FFFF';
const STYLE_OPTIONS = {
  pointsize: ['2'],
  pointshape: ["circle"],
  cb_pointcolors:["PROCESS","P01;P02;P03;P04;P05;P06;P07;P08;P09;P10","FF0000;a83c32;800000;808080;a5c33c;f8ba00;f8cf00;afeeee;00438c;F08080"],
  // FF0000;a83c32;dddddd;808080;a5c33c;f8ba00;f8cf00;afeeee;00438c;F08080
  // "800000;8B0000;A52A2A;B22222;DC143C;FF0000;FF6347;FF7F50;CD5C5C;F08080"
  // pointcolor: ['543c8d'],
};
const MARGIN_DEFAULT = {
  left: 50,
  right: 5,
  bottom: 50,
  top: 10,
};
// const width = 800, height = 300;

const UPDATE_DELAY = 500; // ms
const MAX_NON_NUMERIC_VALUES = 100;
const ADAPTIVE_FORMATTING = 'smart_date';
const GATHER_RANGE = 5; // px

const NUMERIC_TYPES = ['float', 'long', 'int', 'double'];
function isNumeric(type) {
  return NUMERIC_TYPES.includes(type);
}

function merge(o1 = {}, o2) {
  Object.keys(o2).forEach(key => {
    if (o1[key] == null) {
      o1[key] = o2[key];
    }
  });

  return o1;
}

const multiFormat = d3.scaleTime().tickFormat();
function dateConvert(fromFormat, toFormat) {
  const parse =
    typeof fromFormat === 'function' ? fromFormat : d3.timeParse(fromFormat);
  const format =
    typeof toFormat === 'function' ? toFormat : d3.timeFormat(toFormat);
  return date => format(parse(date));
}

class Scatter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      table: "ChartExample",
      viewName: "view_L06P04LOT034",
      chart_condition: "LINE='L06'",  // and PROCESS='P04' and LOT_ID:'LOT03'"

      mode: null,
      filterShape: null,
      error: null,
      popupMode: false,
      popupX: 0,
      popupY: 0,
      popupLoading: false,
      popupStyle: null,
      popupVisible: false,
      data: [],
    };

    this.nextId = 0;

    this.clearDrawMode = this.clearDrawMode.bind(this);
    this.cancelDrawMode = this.cancelDrawMode.bind(this);
    this.onDrawEnd = this.onDrawEnd.bind(this);

    this.updateScatterplot = debounce(
      this.updateScatterplot.bind(this),
      UPDATE_DELAY
    );

    this.updateAxes = this.updateAxes.bind(this);
    this.gatherData = this.gatherData.bind(this);
    this.zoom = this.zoom.bind(this);
    this.zoomEnd = this.zoomEnd.bind(this);

    this.getCurrentTransform = this.getCurrentTransform.bind(this);
    this.setCurrentTransform = this.setCurrentTransform.bind(this);
    this.autofitView = this.autofitView.bind(this);
    this.clearError = this.clearError.bind(this);
    this.filter = this.filter.bind(this);

    this.enableCircle = this.enableCircle.bind(this);
    this.enableRectangle = this.enableRectangle.bind(this);
    this.enablePolygon = this.enablePolygon.bind(this);
    this.enableFreehand = this.enableFreehand.bind(this);
    this.enablePopupMode = this.enablePopupMode.bind(this);

    this.hidePopup = this.hidePopup.bind(this);
    this.centerOnPopup = this.centerOnPopup.bind(this);
    this.generatePopupStyle = this.generatePopupStyle.bind(this);
    this.createAxis = this.createAxis.bind(this);
  }



  componentDidMount() {
    console.log( "componentDidMount...Starting" );
    const { gpudb, viewName, condition, } = this.props;
    const opt = {"collection_name":"__SAMSUNG_TMP"};

    gpudb.has_table( viewName ).then( resp => {
      console.log("has_table :" + JSON.stringify(resp));
      if( resp.table_exists ) {
        this.createAxis();
      }  
      else {
        gpudb.filter( "ChartExample", viewName, condition, opt ).then( view => {
          this.createAxis();
          
          let rowCount = { "viewName": viewName, "rowCount":  view.count };
          this.props.updateRowCount( rowCount );
        });
      }  
    });
    console.log( "componentDidMount End");
  }  
    

     
 
  createAxis() {
    console.log( "createAxis..." );
    const { 
            gpudb, viewName, 
            columnY, columnX,
            lastTransform,
            maxX = null,
            minX = null,
            maxY = null,
            minY = null,
            width,
            height 
          } = this.props;
    const margin = MARGIN_DEFAULT;  
    // Chart size depends on margin.
    const chartWidth = width - margin.left - margin.right;
    const chartHeight = height - margin.top - margin.bottom;  

    console.log("drawMode" + this.state.drawMode);
    const div = d3.select("#" + viewName); 
    div.node();  

    const zoom = d3
                .zoom()
                .clickDistance(5)
                // Zoom controls should not be active while drawing.
                .filter(_ => {
                  return (
                    !d3.event.button &&
                    !this.state.drawMode &&
                    (isNumeric(this.state.typeX) || isNumeric(this.state.typeY))
                  );
                })
                .on('zoom', this.zoom)
                .on('end', this.zoomEnd);

    d3.select("#" +viewName)
      .on('click', this.gatherData)
      .call(zoom)
      // Disable double-click zoom  because it interferes with pop-up click.
      .on('dblclick.zoom', null);
    

      
    gpudb
      // Get types for given table.  This is used to determine numeric vs non-numeric scales.
      // It is also used for how to get the range of values for the column (max/min or uniques).
      .show_table(viewName, {})
      .then(info => {
        try {
          let typeX = null;
          let typeY = null;
          let typePropsX = null;
          let typePropsY = null;
          const types = JSON.parse(info.type_schemas[0]);
          const properties = info.properties[0];

          // Extract column types.
          types.fields.forEach(t => {
            if (t.name === columnX) {
              typeX = t.type;
            }

            if (t.name === columnY) {
              typeY = t.type;
            }
          });
          // Extract column properties.
          Object.entries(properties).forEach(([col, props]) => {
            if (col === columnX) {
              typePropsX = props;
            }

            if (col === columnY) {
              typePropsY = props;
            }
          });

          let aggX = null;
          let domainX = null;
          if (isNumeric(typeX)) {
            // Numeric columns support min/max.
            aggX = gpudb.aggregate_min_max(viewName, columnX, {});

            // Use min/max overrides if possible.
            if (minX !== null && maxX !== null) {
              domainX = [minX, maxX];
            }
          } else if (minX !== null && maxX !== null) {
            // Use min/max overrides for gathering unique values.
            // In this case, they affect the offset and total.
            aggX = gpudb.aggregate_unique(
              viewName,
              columnX,
              minX,
              maxX - minX + 1,
              {}
            );
          } else {
            // Gather as many unique values as reasonable.
            aggX = gpudb.aggregate_unique(
              viewName,
              columnX,
              0,
              MAX_NON_NUMERIC_VALUES,
              {}
            );
          }

          let aggY = null;
          let domainY = null;
          if (isNumeric(typeY)) {
            // Numeric columns support min/max.
            aggY = gpudb.aggregate_min_max(viewName, columnY, {});

            // Use min/max overrides if possible.
            if (minY !== null && maxY !== null) {
              domainY = [minY, maxY];
            }
          } else if (minY !== null && maxY !== null) {
            // Use min/max overrides for gathering unique values.
            // In this case, they affect the offset and total.
            aggY = gpudb.aggregate_unique(
              viewName,
              columnY,
              minY,
              maxY - minY + 1,
              {}
            );
          } else {
            // Gather as many unique values as reasonable.
            aggY = gpudb.aggregate_unique(
              viewName,
              columnY,
              0,
              MAX_NON_NUMERIC_VALUES,
              {}
            );
          }

          return Promise.all([
            aggX,
            aggY,
            Promise.resolve({
              domainX,
              domainY,
              typeX,
              typeY,
              typePropsX,
              typePropsY,
            }),
          ]);


        }  catch (e) { 
          // This should only be triggered when the response is in a shape we don't expect.
          // comment out by KHS
          const newError = new Error('Could not handle Kinetica response.');
          newError.cause = e;
          // newError.causeType = ErrorCauses.Internal;
          throw newError;
        }
      })
      .then(([xAggs, yAggs, params]) => {
        const {
          domainX,
          domainY,
          typeX,
          typeY,
          typePropsX,
          typePropsY,
        } = params;
        let scaleX;
        if (isNumeric(typeX)) {
          // Use a linear scale.
          scaleX = d3
            .scaleLinear()
            // Prefer given domain over given min/max over computed min/max.
            .domain( domainX || [xAggs.min, xAggs.max])   //domainXProp ||
            .range([0, chartWidth]);
        } else {
          // Prefer given domain over computed uniques.
          const domain = xAggs.data.column_1;   //domainXProp || 
          // Compute range as equally spaced domain values.
          const range = domain.map(
            (_, i) => i * (chartWidth / (domain.length - 1))
          );

          // Use an ordinal scale.
          scaleX = d3
            .scaleOrdinal()
            .domain(domain)
            .range(range);
        }

        let scaleY;
        if (isNumeric(typeY)) {
          // Use a linear scale.
          scaleY = d3
            .scaleLinear()
            // Prefer given domain over given min/max over computed min/max.
            .domain( domainY || [yAggs.min, yAggs.max])    //domainYProp ||
            // NOTE: The range is from height to 0 instead of traditionally 0 to height.
            //       This is because the svg origin is the top left whereas Kinetica's
            //       scatterplot origin is bottom left.  This switch ensures that labels
            //       and points match correctly.
            .range([chartHeight, 0]);


          const yMinMax = { "viewName": viewName, "minValue":  yAggs.min, "maxValue": yAggs.max };
          this.props.updateChartMinMax( yMinMax );
 
        } else {
          // Prefer given domain over computed uniques.
          const domain =  yAggs.data.column_1;    //domainYProp ||
          // Compute range as equally spaced domain values.
          const range = domain.map(
            (_, i) => i * (chartHeight / (domain.length - 1))
          );

          // Use an ordinal scale.
          scaleY = d3
            .scaleOrdinal()
            .domain(domain)
            // NOTE: The range is reversed rather than used normally.
            //       This is because the svg origin is the top left whereas Kinetica's
            //       scatterplot origin is bottom left.  This reversal ensures that
            //       labels and points match correctly.
            .range(range.reverse());
        }

        this.setState(
          {
            chartWidth,
            chartHeight,
            scaleX,
            scaleY,
            typeX,
            typeY,
            typePropsX,
            typePropsY,
            zoom,
          },
          _ => {
            this.updateAxes();

            // Set transform of viewport if given one.
            if (lastTransform) {
              zoom.transform(
                d3.select("#" + viewName).select('.viewport'),
                d3.zoomIdentity
                  .translate(lastTransform.x, lastTransform.y)
                  .scale(lastTransform.k)
              );
            }
            
          }
        );
      })
      .catch(err => {
        // This should catch errors when calling the above Kinetica endpoints.
        const newError = new Error('Failed to initialize scatterplot.');
        // this.creatViewAndScatter();
        if (err.causeType) {
          newError.cause = err;
          newError.causeType = err.causeType;
        } else {
          newError.cause = err;
          // KHS newError.causeType = ErrorCauses.Kinetica;
        }
        this.setState({
          error: newError,
        });
      } 
    );
  }

  componentDidUpdate() {
    this.updateScatterplot(this.getCurrentTransform());
  }

  autofitView() {
    const transform = this.getCurrentTransform();
    // Create transform to center on popup location.

    console.log("centerOnPopup :" + transform);

    const { columnX, columnY, gpudb, table, onFit } = this.props;
    const { typeX, typeY } = this.state;

    let aggX = null;
    if (isNumeric(typeX)) {
      // Numeric columns support min/max.
      aggX = gpudb.aggregate_min_max(table, columnX, {});
    } else {
      // Gather as many unique values as reasonable.
      aggX = gpudb.aggregate_unique(
        table,
        columnX,
        0,
        MAX_NON_NUMERIC_VALUES,
        {}
      );
    }

    let aggY = null;
    if (isNumeric(typeY)) {
      // Numeric columns support min/max.
      aggY = gpudb.aggregate_min_max(table, columnY, {});
    } else {
      // Gather as many unique values as reasonable.
      aggY = gpudb.aggregate_unique(
        table,
        columnY,
        0,
        MAX_NON_NUMERIC_VALUES,
        {}
      );
    }

    Promise.all([aggX, aggY]).then(([x, y]) => {
      const { scaleX, scaleY } = this.state;
      let newScaleX = scaleX.copy();
      if (isNumeric(typeX)) {
        newScaleX.domain([x.min, x.max]);
      } else {
        newScaleX.domain(x.data.column_1);
      }

      let newScaleY = scaleY.copy();
      if (isNumeric(typeY)) {
        newScaleY.domain([y.min, y.max]);
      } else {
        newScaleY.domain(y.data.column_1);
      }

      this.setState(
        {
          scaleX: newScaleX,
          scaleY: newScaleY,
        },
        _ => {
          this.setCurrentTransform(d3.zoomIdentity);
          if (onFit) {
            onFit(newScaleX, newScaleY);
          }
        }
      );
    });
  }

  clearError() {
    this.setState({
      error: null,
    });
  }

  enableCircle() {
    this.setState({
      popupMode: false,
      drawMode: DrawLayer.DrawMode.Circle,
    });
  }

  enableRectangle() {
    this.setState({
      popupMode: false,
      drawMode: DrawLayer.DrawMode.Rectangle,
    });
  }

  enablePolygon() {
    this.setState({
      popupMode: false,
      drawMode: DrawLayer.DrawMode.Polygon,
    });
  }

  enableFreehand() {
    this.setState({
      popupMode: false,
      drawMode: DrawLayer.DrawMode.Freehand,
    });
  }

  
  enablePopupMode() {
    this.setState({
      popupMode: true,
      drawMode: null,
    });
  }

  gatherData() {
    console.log("gatherData Starting...")

    const {
      columnX, columnY, gpudb, viewName,
    } = this.props;
  
    const {
      popupMode,
      scaleX,
      scaleY,
      typeX,
      typeY,
      typePropsX,
      typePropsY,
    } = this.state;

    // TODO: Figure out if allowing non-numeric info pop-up would be complicated.
    if (!isNumeric(typeX) || !isNumeric(typeY) || !popupMode) {
      return;
    }

    let [x, y] = d3.mouse(d3.event.target);
    console.log("x:" + x);
    console.log("y:" + y);

    console.log(scaleX);
    console.log(scaleY);

    const transform = this.getCurrentTransform();
    const newScaleX = transform.rescaleX(scaleX);
    const newScaleY = transform.rescaleY(scaleY);

    console.log(transform);
    console.log(newScaleX);
    console.log(newScaleY);

    // Computer x, x ranges
    let xRangeUpper = newScaleX.invert(x + GATHER_RANGE);
    let xRangeLower = newScaleX.invert(x - GATHER_RANGE);
    let yRangeUpper = newScaleY.invert(y - GATHER_RANGE);
    let yRangeLower = newScaleY.invert(y + GATHER_RANGE);

    // Need to round long|timestamp columns
    if (typePropsX.indexOf('timestamp') > -1) {
      xRangeUpper = Math.round(xRangeUpper);
      xRangeLower = Math.round(xRangeLower);
    }
    if (typePropsY.indexOf('timestamp') > -1) {
      yRangeUpper = Math.round(yRangeUpper);
      yRangeLower = Math.round(yRangeLower);
    }

    // Gather any data that is within GATHER_RANGE of pixels from mouse click.
    const expressionX = `${columnX} <= ${xRangeUpper} AND ${columnX} >= ${xRangeLower}`;
    const expressionY = `${columnY} <= ${yRangeUpper} AND ${columnY} >= ${yRangeLower}`;
    const expression = `${expressionX} AND ${expressionY}`;

    const popupStyle = this.generatePopupStyle(x, y);

    this.setState({
      popupX: transform.invertX(x),
      popupY: transform.invertY(y),
      popupStyle,
      popupVisible: true,
      popupLoading: true,
      popupArrowPlacement: 'top-left',
    });

    alert(expression);
    // NOTE: Limited to 100 results right now.
    gpudb
      .get_records(viewName, 0, 100, { expression })
      .then(response => {
        console.log(response);
        this.setState(
          {
            popupVisible: response.data.length > 0,
            popupLoading: false,
            data: response.data,
          },
          this.centerOnPopup
        );
      })
      .catch(err => {
        const newError = new Error('Failed to open pop-up.');
        newError.cause = err;
        //newError.causeType = ErrorCauses.Kinetica;
        this.setState({
          error: newError,
          popupVisible: false,
          popupLoading: false,
        });
      });
  }

  getCurrentTransform() {
    const { viewName } = this.props;
    return d3.zoomTransform(
      d3
        .select("#"+viewName)
        //.select('.viewport_root')   KHS
        .node()  //KHS
    );
  }

  setCurrentTransform(transform, delay = 0, duration = 0) {
    
    console.log("setCurrentTransform");

    const { viewName } = this.props;
    const { zoom } = this.state;
    d3.select("#"+viewName)
      .select('.viewport')
      .transition()
      .delay(delay)
      .duration(duration)
      .call(zoom.transform, transform);
  }

    // Recompute axes and titles.
  updateAxes(transform, newScaleX, newScaleY) {
    console.log("UpdateAxes Starting...")
    const {
      columnX,
      columnY,
      formatX: formatXProp,
      formatY: formatYProp,
      //parentSelector,
      titleX,
      titleY,
    } = this.props;

    const parentSelector = "#" + this.props.viewName;
    const {
      chartWidth,
      chartHeight,
      scaleX,
      scaleY,
      typeX,
      typeY,
      typePropsX,
      typePropsY,
    } = this.state;

    const TYPE_STRING = 'string';
    const TYPE_TIMESTAMP = 'timestamp';

    let formatX = null;
    if (typePropsX.includes(TYPE_TIMESTAMP)) {
      if (formatXProp === ADAPTIVE_FORMATTING) {
        formatX = dateConvert('%Q', multiFormat);
      } else {
        formatX = dateConvert('%Q', formatXProp || '%x');
      }
    } else if (typeX !== TYPE_STRING) {
      formatX = d3.format(formatXProp || '~s');
    }

    let formatY = null;
    if (typePropsY.includes(TYPE_TIMESTAMP)) {
      if (formatYProp === ADAPTIVE_FORMATTING) {
        formatY = dateConvert('%Q', multiFormat);
      } else {
        formatY = dateConvert('%Q', formatYProp || '%x');
      }
    } else if (typeY !== TYPE_STRING) {
      formatY = d3.format(formatYProp || '~s');
    }

    const axisX = d3
      .axisBottom(
        transform && isNumeric(typeX)
          ? transform.rescaleX(scaleX)
          : newScaleX || scaleX
      )
      .tickFormat(formatX)
      // Extend ticks full height.
      .tickSize(-chartHeight);

    const axisY = d3
      .axisLeft(
        transform && isNumeric(typeY)
          ? transform.rescaleY(scaleY)
          : newScaleY || scaleY
      )
      .tickFormat(formatY)
      // Extend ticks full width.
      .tickSize(-chartWidth);
    const parent = d3.select(parentSelector);

    // Update axes and axis titles.
    parent.select('.xaxis').call(axisX);
    parent.select('.xtitle text').text(titleX || columnX);
    parent.select('.yaxis').call(axisY);
    parent.select('.ytitle text').text(titleY || columnY);

    // Rotate non-numeric x-axis labels if it is crowded.  The metric we use
    // is if there are more than 20 labels.
    // Note: This might be overly specific with it's used of pixel offsets.
    if (!isNumeric(typeX) && (newScaleX || scaleX).domain().length > 20) {
      parent
        .select('.xaxis')
        .selectAll('text')
        .attr('transform', 'rotate(90)')
        .attr('dx', '2px')
        .attr('dy', '-1px')
        .style('text-anchor', 'start');
    }
  }

  // Sends filter based on drawn shape.
  filter() {
    const { columnX, columnY, onFilter, viewName } = this.props;
    const { filterShape, scaleX, scaleY } = this.state;
    if (onFilter) {
      // Clear filter if no shape drawn.
      if (!filterShape) {
        onFilter(null);
      } else if (filterShape.type === ShapeType.Rectangle) {
        const x1 = scaleX.invert(filterShape.x);
        const y1 = scaleY.invert(filterShape.y);
        const x2 = scaleX.invert(filterShape.x + filterShape.width);
        const y2 = scaleY.invert(filterShape.y + filterShape.height);
        onFilter({
          columnX,
          columnY,
          xArr: [x2, x1, x1, x2],
          yArr: [y1, y1, y2, y2],
          type: filterShape.type,
          id: viewName,
        });
      } else if (filterShape.type === ShapeType.Polygon) {
        const valuesX = filterShape.points.map(p => scaleX.invert(p[0]));
        const valuesY = filterShape.points.map(p => scaleY.invert(p[1]));
        onFilter({
          columnX,
          columnY,
          xArr: valuesX,
          yArr: valuesY,
          type: filterShape.type,
          id: viewName,
        });
      }
    }
  }

  zoom() {
    console.log("zoom Starting...")
    const { onZoom, viewName } = this.props;
    const { drawMode, popupX, popupY } = this.state;

    if (!drawMode) {
      const g = d3.select("#"+viewName).select('.viewport_root');
      g.attr('transform', d3.event.transform);
      this.updateAxes(d3.event.transform);

      // Updates popup by moving it as viewport changes.
      const style = this.generatePopupStyle(popupX, popupY, d3.event.transform);
      const popup = d3.select("#" + viewName ).select('.k-scatter-popup');
      if (!popup.empty()) {
        popup.style('top', style.top);
        popup.style('left', style.left);
      }
      //* KHS
      if (onZoom) {
        onZoom(d3.event.transform);
      }
       
    }
  } 

  zoomEnd() {
    const { drawMode } = this.state;
    if (!drawMode) {
      this.updateScatterplot(d3.event.transform);
    }
  }

  clearDrawMode() {
    this.setState(
      {
        popupMode: false,
        drawMode: null,
        filterShape: null,
      },
      _ => this.filter()
    );
  }

  cancelDrawMode() {
    this.setState({
      popupMode: false,
      drawMode: null,
    });
  }

  // Handles a new filter shape.
  onDrawEnd(shape) {
    // Get current transform from viewport
    const transform = this.getCurrentTransform();
    // Need to transform incoming shape into viewport coordinates.
    switch (shape.type) {
      case ShapeType.Circle:
        shape.cx = transform.invertX(shape.cx);
        shape.cy = transform.invertY(shape.cy);
        shape.r = shape.r / transform.k;
        break;
      case ShapeType.Rectangle:
        shape.x = transform.invertX(shape.x);
        shape.y = transform.invertY(shape.y);
        shape.width = shape.width / transform.k;
        shape.height = shape.height / transform.k;
        break;
      case ShapeType.Polygon:
        shape.points = shape.points.map(p => transform.invert(p));
        break;
      default:
        break;
    }
    this.setState(
      {
        filterShape: shape,
        drawMode: null,
      },
      _ => this.filter()
    );
  }


  centerOnPopup() {
    const { viewName } = this.props;
    const { chartWidth, chartHeight, popupX, popupY, data } = this.state;
    if (data.length > 0) {
      const popup = d3.select("#"+viewName).select('.k-scatter-popup');
      let offsetX = 0;
      let offsetY = 0;
      // Need to get size of pop-up so we can center on its local center.

      
      if (!popup.empty()) {
        alert(popup.node().offsetWidth);
        offsetX = popup.node().offsetWidth / 2;
        offsetY = popup.node().offsetHeight / 2;
      }
      
      const transform = this.getCurrentTransform();
      // Create transform to center on popup location.

      console.log("centerOnPopup :" + transform);

      const newTransform = d3.zoomIdentity
        .translate(
          chartWidth / 2 - offsetX - popupX * transform.k,
          chartHeight / 2 - offsetY - popupY * transform.k
        )
        .scale(transform.k);
      
        console.log("centerOnPopup newTransform:" + newTransform);  
      this.setCurrentTransform(newTransform, 100, 500);
    }
  }

  generatePopupStyle(x, y, transform) {
    let screenX = 0;
    let screenY = 0;
    // The offsets applied to the screen coordinates are
    // based on the arrow size defined in the Popup CSS.
    console.log("generatePopupStyle :" + transform);

    if (transform) {
      screenX = transform.applyX(x) - 10;
      screenY = transform.applyY(y) + 10;
    } else {
      screenX = x - 10;
      screenY = y + 10;
    }

    const popupStyle = {
      left: `${screenX}px`,
      top: `${screenY}px`,
    };

    return popupStyle;
  }
  //  <p> : {this.props.condition} </p>

  updateScatterplot(transform = d3.zoomIdentity) {
    const {
      gpudb,
      viewName,
      columnX,
      columnY,
      loading,
      minX = null,
      minY = null,
      styleOptions,
      autoScale,
      autoScaleFactor,
    } = this.props;
    const {
      chartWidth,
      chartHeight,
      scaleX,
      scaleY,
      typeX,
      typeY,
    } = this.state;
    const invK = 1 / transform.k;

    console.log("updateScatterPlot :" + transform);

    // Skip rerendering scatterplot in very bad case of scaleX and scaleY missing.
    if (!scaleX || !scaleY) {
      return;
    }

    // Compute min/max x bounds.
    // NOTE: Variables are named left, right instead of minX, maxX because of name
    //       clashes with props.
    let left, right;
    if (isNumeric(typeX)) {
      const transformScaleX = transform.rescaleX(scaleX);
      left = transformScaleX.invert(0);
      right = transformScaleX.invert(chartWidth);
    } else {
      left = minX !== null ? minX : 0;
      right = left + scaleX.domain().length - 1;
    }

    // Compute min/max y bounds.
    // NOTE: Variables are named top, bottom instead of minY, maxY because of name
    //       clashes with props.
    let top, bottom;
    if (isNumeric(typeY)) {
      const transformScaleY = transform.rescaleY(scaleY);
      top = transformScaleY.invert(chartHeight);
      bottom = transformScaleY.invert(0);
    } else {
      top = minY !== null ? minY : 0;
      bottom = top + scaleY.domain().length - 1;
    }

console.log(autoScaleFactor);
    const styleOpts = STYLE_OPTIONS;  //merge({ ...styleOptions }, 
    
    if (autoScale) {
      styleOpts.pointsize = [
        String(
          Math.min(
            Math.max(
              Math.round(
                Number(styleOpts.pointsize[0]) *
                  transform.k *
                  (autoScaleFactor || 1) *
                  0.5
              ),
              1
            ),
            20  // KHS 20 -> 2 
          )
        ),
      ];
    }
    

    console.log(styleOpts);
    if (this.loadingTimeout) {
      clearTimeout(this.loadingTimeout);
    }

    // Don't show loading icon if request completes very quickly.
    /* KHS 
    this.loadingTimeout = setTimeout(_ => {
      loading.show({ forceShow: true });
      this.loadingTimeout = null;
    }, 1000);
    */
    console.log("updateScatterplot before making image chart : calling gpudb.visualize image chart");
    gpudb
      .visualize_image_chart(
        viewName,
        [columnX],
        [columnY],
        left,
        right,
        top,
        bottom,
        Math.round(chartWidth),
        Math.round(chartHeight),
        BG_COLOR,
        styleOpts,
        {}
      )
      .then(data => {
        /* KHS
        if (this.loadingTimeout) {
          clearTimeout(this.loadingTimeout);
          this.loadingTimeout = null;
        }
        */

        // TODO: Figure out how to load axes information into scales
        //       without requiring re-render, but updating scales.
        /*
        const axes = data.axes_info;
        let newScaleX = null;
        if (!isNumeric(typeX)) {
          newScaleX = scaleX
            .copy()
            .domain(axes.sorted_x_values)
            .range(axes.location_x);
        }

        let newScaleY = null;
        if (!isNumeric(typeY)) {
          newScaleY = scaleY
            .copy()
            .domain(axes.sorted_y_values)
            .range(axes.location_y);
        }

        this.updateAxes(
          d3.zoomTransform(d3.select('#viewport').node()),
          newScaleX,
          newScaleY
        );
        */

        console.log("updateScatterplot d3.select and image : After getting return data - gpudb.visualize image chart");
        const image = d3.select("#"+viewName).select('.scatterplot_render');

        console.log("updateScatterplot" + transform);
        image
          .attr('href', `data:image/png;base64,${btoa(data.image_data)}`)
          .attr('width', chartWidth * invK)
          .attr('height', chartHeight * invK)
          .attr('x', transform.invertX(0))
          .attr('y', transform.invertY(0));

      })
      .catch(err => {
        console.log(err);
        // loading.hide();
        // TODO: Figure out best way of showing scatterplot errors without
        //       causing render of scatterplot which may get into infinite
        //       loop if scatterplot request keeps failing.
        // const newError = new Error('Failed to update scatterplot.');
        // newError.cause = err;
        // newError.causeType = ErrorCauses.Kinetica;
        // this.setState({
        //   error: newError,
        // });
      });
  }

  hidePopup() {
    this.setState({
      popupVisible: false,
    });
  }

  render() {
    const {
      width,
      height,
      margin: marginProp,
      filterShape: filterShapeProp,
      styleOptions,
      template,
      disableLegend,
    } = this.props;

    const {
      chartWidth = 0,
      chartHeight = 0,
      data,
      drawMode = true,
      filterShape: filterShapeState,
      scaleX,
      scaleY,
      typeX,
      typeY,
      error,
      popupMode,
      popupLoading,
      popupStyle,
      popupVisible,
    } = this.state;

    const margin = MARGIN_DEFAULT;   //  merge(marginProp, )
    const styleOpts = merge(styleOptions, STYLE_OPTIONS);

    // Filter shape can either be in state or from props.
    let filterShape = filterShapeState;

    // Transform filter shape when viewport/scales have changed.
    // NOTE: This logic depends on the slice refreshing this component.
    
    if (filterShapeProp && !filterShapeState && scaleX && scaleY) {
      const { xArr, yArr } = filterShapeProp;

      // Compute new range from old domain values.
      const transformedXArr = xArr.map(x => scaleX(x));
      const transformedYArr = yArr.map(y => scaleY(y));
      filterShape = { type: ShapeType.Polygon };
      filterShape.points = transformedXArr.map((x, i) => [
        x,
        transformedYArr[i],
      ]);
    }
    
    return (
      <div className="row chart-container">
        <input type="hidden" value="false" />
        <div style={{ width: '45%', height: '100%',}}>
          <div style={{ position: 'relative' }}>
            <Toolbar
                onCircle={this.enableCircle}
                onRectangle={this.enableRectangle}
                onPolygon={this.enablePolygon}
                onFreehand={this.enableFreehand}
                onClear={this.clearDrawMode}
                onCancel={this.cancelDrawMode}
                onInfo={this.enablePopupMode}
                onAutofit={this.autofitView}
                top={margin.top + 5}
                left={margin.left + chartWidth - 5 - 28}
                drawMode={drawMode}
                popupMode={popupMode}
            />
            <ClassbreakLegend
              cbColor={styleOpts.cb_pointcolors}
              style={{
                position: 'absolute',
                top: `${margin.top + 5}px`,
                left: `${margin.left + 10}px`,
                pointerEvents: 'none',
                fontSize: '12px',
              }}
            />
            {template && popupVisible && (
              // This div acts as a viewport, similar to the svg.
              <div
                style={{
                  position: 'absolute',
                  top: `${margin.top}px`,
                  left: `${margin.left}px`,
                  width: `${chartWidth}px`,
                  height: `${chartHeight}px`,
                  pointerEvents: 'none',
                  overflow: 'hidden',
                }}
              >
                <Popup
                  arrowPlacement="top"
                  data={data}
                  loading={popupLoading}
                  template={template}
                  onClose={this.hidePopup}
                  style={popupStyle}
                />
              </div>
            )}
          </div>
            <svg
              className="svg_root"
              id={this.props.viewName}
              width={width} height={height}
            >
              <svg className="viewport"
                width={chartWidth}
                height={chartHeight}
                x={margin.left}
                y={margin.top}
              >
                <g className="viewport_root">
                  <image className="scatterplot_render" />
                  <g className="shape_root">
                  {!drawMode && (
                    <DrawLayer.Shape
                      {...filterShape}
                      fill="#bbdefb"
                      stroke="#2196f3"
                      opacity="0.2"
                    />
                  )}
                </g>
                </g>
                <DrawLayer
                  width={chartWidth}
                  height={chartHeight}
                  drawMode={drawMode}
                  fill="#bbdefb"
                  stroke="#1976d2"
                  onDrawEnd={this.onDrawEnd}
                />
              </svg>
              <g
                className="xaxis"
                transform={d3.zoomIdentity.translate(
                  margin.left,
                  chartHeight + margin.top
                )}
                style={{ pointerEvents: 'none' }}
              />
              <g
                className="yaxis"
                transform={d3.zoomIdentity.translate(margin.left, margin.top)}
                style={{ pointerEvents: 'none' }}
              />
              <g
                className="xtitle"
                transform={d3.zoomIdentity.translate(
                  margin.left + chartWidth / 2,
                  chartHeight +  margin.bottom    //height
                )}
              >
                <text fill="black" textAnchor="middle" y="-1em" />
              </g>
              <g
                className="ytitle"
                transform={
                  // NOTE: Need to rotate -90 degrees AFTER translation so
                  // default zoomIdentity.toString() can't be used as is.
                  d3.zoomIdentity
                    .translate(0, margin.top + chartHeight / 2)
                    .toString() + ' rotate(-90)'
                }
              >
                <text fill="black" textAnchor="middle" y="1em" />
              </g>
            </svg>  
          </div>    
      </div>
    ) 
  }
}

function mapDispatchToProps(dispatch) {
  return {
    updateChartMinMax  : minMax   => dispatch( updateChartMinMax( minMax )), 
    updateRowCount     : rowCount => dispatch( updateRowCount( rowCount ))
  };
}

export default connect( null, mapDispatchToProps )( Scatter );