import React, { Component } from 'react';
import bracket from 'bracket-templates';
import moment from 'moment';
import striptags from 'striptags';
import URI from 'urijs';
import CheckImage from './CheckImage';
import './popup.css';

const MODAL_TIMESTAMP_FORMAT = 'MM/DD/YY hh:mm:ss A';
const VALID_ARROW_PLACEMENTS = [
  'top-left',
  'top-right',
  'bottom-left',
  'bottom-right',
  'left-top',
  'left-bottom',
  'right-top',
  'right-bottom',
];

const genLinkFromURL = url =>
  '<a href="' + url + '" target="_blank">' + url + '</a>';

class Popup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      index: 0,
    };

    this.next = this.next.bind(this);
    this.prev = this.prev.bind(this);
    this.close = this.close.bind(this);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.data !== this.props.data) {
      this.setState({
        index: 0,
      });
    }
  }

  next(e) {
    const { data } = this.props;
    e.preventDefault();
    this.setState(prevState => ({
      index: Math.min(prevState.index + 1, data.length - 1),
    }));
  }

  prev(e) {
    e.preventDefault();
    this.setState(prevState => ({
      index: Math.max(0, prevState.index - 1),
    }));
  }

  close(e) {
    const { onClose } = this.props;
    e.preventDefault();
    if (onClose) {
      onClose();
    }
  }

  render() {
    const {
      data: dataProp,
      template,
      style,
      onClose,
      arrowPlacement,
      loading,
      ...otherProps // eslint-disable-line comma-dangle
    } = this.props;
    const { index } = this.state;

    let classes = 'k-scatter-popup panel panel-default';
    if (arrowPlacement === 'top') {
      classes += ' k-arrow-top-left';
    } else if (arrowPlacement === 'left') {
      classes += ' k-arrow-left-top';
    } else if (arrowPlacement === 'bottom') {
      classes += ' k-arrow-bottom-left';
    } else if (arrowPlacement === 'right') {
      classes += ' k-arrow-right-top';
    } else if (VALID_ARROW_PLACEMENTS.includes(arrowPlacement)) {
      classes += ` k-arrow-${arrowPlacement}`;
    }

    if (loading) {
      return (
        <div style={style} className={classes} {...otherProps}>
          <div className="panel-heading clearfix">
            <button
              className="k-scatter-view-button"
              style={{ float: 'right', color: '#999' }}
              onClick={this.close}
            >
              <i className="fa fa-times" />
            </button>
          </div>
          <div
            className="panel-body k-popup-loading"
            style={{ width: '200px', height: '100px' }}
          >
            <i className="fa fa-circle-o-notch fa-spin fa-3x fa-fw" />
          </div>
          <div className="panel-footer">
            <button
              className="k-scatter-view-button"
              onClick={this.prev}
              disabled
            >
              <i className="fa fa-arrow-left" />
            </button>
            <span className="k-scatter-view-index">? of ?</span>
            <button
              className="k-scatter-view-button"
              onClick={this.next}
              disabled
            >
              <i className="fa fa-arrow-right" />
            </button>
          </div>
        </div>
      );
    } else if (template && dataProp.length > 0) {
      const data = dataProp[index];

      // Parse and inject
      for (const key in data) {
        if (typeof data[key] === 'string') {
          // Check for images
          const image = CheckImage(data[key]);
          if (image) {
            const body =
              'data:' + image.contentType + ';base64,' + image.base64String;
            data[key] = body;
          } else {
            let body = URI.withinString(striptags(data[key]), genLinkFromURL);
            body = body.replace(/\\u([0-9a-f]{8})/gi, (m, p) => {
              let newP = p;
              while (newP.length > 1 && newP.substr(0, 1) === '0') {
                newP = newP.substr(1);
              }
              return (
                '<img height="14" width="14" hspace="3"' +
                'src="https://abs.twimg.com/emoji/v1/72x72/' +
                newP +
                '.png" draggable="false" />'
              );
            });
            const regex = /\\u([0-9a-f]{4})/gi;
            body = body.replace(regex, (m, p) =>
              String.fromCharCode(parseInt(p, 16))
            );
            data[key] = body;
          }
        } else if (typeof data[key] === 'number') {
          if (data[key].toString().length === 13) {
            data[key] = moment(new Date(data[key])).format(
              MODAL_TIMESTAMP_FORMAT
            );
          }
        }
      }

      const formatted = bracket.render(template, data);

      return (
        <div style={style} className={classes} {...otherProps}>
          <div className="panel-heading clearfix">
            <button
              className="k-scatter-view-button"
              style={{ float: 'right', color: '#999' }}
              onClick={this.close}
            >
              <i className="fa fa-times" />
            </button>
          </div>
          <div className="panel-body">
            <div dangerouslySetInnerHTML={{ __html: formatted }} />
          </div>
          <div className="panel-footer">
            <button
              className="k-scatter-view-button"
              onClick={this.prev}
              disabled={index <= 0}
            >
              <i className="fa fa-arrow-left" />
            </button>
            <span className="k-scatter-view-index">
              {index + 1} of {dataProp.length}
            </span>
            <button
              className="k-scatter-view-button"
              onClick={this.next}
              disabled={index >= dataProp.length - 1}
            >
              <i className="fa fa-arrow-right" />
            </button>
          </div>
        </div>
      );
    }

    return null;
  }
}

export default Popup;
