import React, { Component } from 'react';

const DEFAULT_DELIMITER = ';';

class ClassbreakLegend extends Component {
  render() {
    const { cbColor, ...otherProps } = this.props;

    const ranges = cbColor[1].split(DEFAULT_DELIMITER);
    const colors = cbColor[2].split(DEFAULT_DELIMITER);

    const pairs = colors.map((color, i) => [color, ranges[i]]);
    const labels = pairs.map(([color, range]) => (
      <div key={range} style={{ height: '10px', marginBottom: '5px' }}>
        <span
          style={{
            display: 'inline-block',
            width: '10px',
            height: '10px',
            backgroundColor: '#' + color,
            float: 'left',
            marginRight: '3px',
            borderRadius: '10px',
          }}
        />
        <span
          style={{
            lineHeight: '10px',
            fontSize: '11px',
            float: 'left',
            backgroundColor: '#FFFFFF99',
          }}
        >
          {range}
        </span>
      </div>
    ));

    return <div {...otherProps}>{labels}</div>;
  }
}

export default ClassbreakLegend;
