import React, { Component } from 'react';

class DrawLayer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      drawEnded: null,

      // Circle state
      centerX: null,
      centerY: null,
      radius: null,

      // Rectangle state
      left: null,
      right: null,
      top: null,
      bottom: null,

      // Polygon/Freehand state
      points: null,
      extraPoint: null,
    };

    this.normalizeMouse = this.normalizeMouse.bind(this);
    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseUp = this.onMouseUp.bind(this);
    this.onMouseMove = this.onMouseMove.bind(this);
    this.onClick = this.onClick.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.circleStart = this.circleStart.bind(this);
    this.circleUpdate = this.circleUpdate.bind(this);
    this.circleEnd = this.circleEnd.bind(this);
    this.circleDraw = this.circleDraw.bind(this);
    this.rectangleStart = this.rectangleStart.bind(this);
    this.rectangleUpdate = this.rectangleUpdate.bind(this);
    this.rectangleEnd = this.rectangleEnd.bind(this);
    this.rectangeDraw = this.rectangeDraw.bind(this);
    this.polygonStart = this.polygonStart.bind(this);
    this.polygonAdd = this.polygonAdd.bind(this);
    this.polygonUpdate = this.polygonUpdate.bind(this);
    this.polygonEnd = this.polygonEnd.bind(this);
    this.polygonDraw = this.polygonDraw.bind(this);
    this.freehandDraw = this.freehandDraw.bind(this);
    this.shapeDraw = this.shapeDraw.bind(this);
    this.onClick = this.onClick.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { drawMode } = this.props;
    if (drawMode !== prevProps.drawMode && drawMode === null) {
      this.setState({
        drawEnded: null,

        // Circle state
        centerX: null,
        centerY: null,
        radius: null,

        // Rectangle state
        left: null,
        right: null,
        top: null,
        bottom: null,

        // Polygon/Freehand state
        points: null,
        extraPoint: null,
      });
    }
  }

  /* Handle Mouse Events */
  //#region
  // See: https://stackoverflow.com/a/29296049
  normalizeMouse(target, x, y) {
    const rect = target.getBoundingClientRect();
    return [x - rect.left, y - rect.top];
  }

  onMouseDown(e) {
    if (e.button === DrawLayer.ActiveButton) {
      const [nx, ny] = this.normalizeMouse(e.target, e.clientX, e.clientY);
      const { drawMode } = this.props;
      switch (drawMode) {
        case DrawLayer.DrawMode.Circle:
          this.circleStart(nx, ny);
          break;
        case DrawLayer.DrawMode.Rectangle:
          this.rectangleStart(nx, ny);
          break;
        case DrawLayer.DrawMode.Freehand:
          this.polygonStart(nx, ny);
          break;
        default:
          break;
      }
    }
  }

  onMouseUp(e) {
    if (e.button === DrawLayer.ActiveButton) {
      const [nx, ny] = this.normalizeMouse(e.target, e.clientX, e.clientY);
      const { drawMode } = this.props;
      switch (drawMode) {
        case DrawLayer.DrawMode.Circle:
          this.circleEnd(nx, ny);
          break;
        case DrawLayer.DrawMode.Rectangle:
          this.rectangleEnd(nx, ny);
          break;
        case DrawLayer.DrawMode.Freehand:
          this.polygonEnd(nx, ny);
          break;
        default:
          break;
      }
    }
  }

  onMouseMove(e) {
    const { drawMode } = this.props;
    const [nx, ny] = this.normalizeMouse(e.target, e.clientX, e.clientY);
    if (drawMode === DrawLayer.DrawMode.Polygon) {
      this.polygonUpdate(nx, ny);
    } else if ((e.buttons & DrawLayer.HeldActiveButton) !== 0) {
      switch (drawMode) {
        case DrawLayer.DrawMode.Circle:
          this.circleUpdate(nx, ny);
          break;
        case DrawLayer.DrawMode.Rectangle:
          this.rectangleUpdate(nx, ny);
          break;
        case DrawLayer.DrawMode.Freehand:
          this.polygonAdd(nx, ny);
          break;
        default:
          break;
      }
    }
  }

  onClick(e) {
    const [nx, ny] = this.normalizeMouse(e.target, e.clientX, e.clientY);
    const { drawMode } = this.props;
    switch (drawMode) {
      case DrawLayer.DrawMode.Polygon:
        const { points } = this.state;
        if (points && points.length > 0) {
          this.polygonAdd(nx, ny);
        } else {
          this.polygonStart(nx, ny);
        }
        break;
      default:
        break;
    }
  }

  onKeyDown(e) {
    const { drawMode } = this.props;
    if (e.key === 'Escape' && drawMode === DrawLayer.DrawMode.Polygon) {
      this.polygonEnd();
    }
  }
  //#endregion

  /* Circle Methods */
  //#region
  circleStart(x, y) {
    this.setState({
      centerX: x,
      centerY: y,
      radius: 0,
      drawEnded: false,
    });
  }

  circleUpdate(x, y) {
    this.setState(prevState => ({
      radius: Math.sqrt(
        Math.pow(prevState.centerX - x, 2) + Math.pow(prevState.centerY - y, 2)
      ),
    }));
  }

  circleEnd() {
    const { onDrawEnd } = this.props;
    const { centerX, centerY, radius } = this.state;
    if (onDrawEnd) {
      onDrawEnd({
        type: Shape.Type.Circle,
        cx: centerX,
        cy: centerY,
        r: radius,
      });
    }

    this.setState({
      centerX: null,
      centerY: null,
      radius: null,
    });
  }

  circleDraw() {
    const { fill, stroke } = this.props;
    const { centerX, centerY, radius } = this.state;
    if (centerX && centerY && radius) {
      return (
        <circle
          cx={centerX}
          cy={centerY}
          r={radius}
          fill={fill}
          stroke={stroke}
          opacity="0.6"
          style={{ pointerEvents: 'none' }}
        />
      );
    }

    return null;
  }
  //#endregion

  /* Rectangle Methods */
  //#region
  rectangleStart(x, y) {
    this.setState({
      left: x,
      right: x,
      top: y,
      bottom: y,
      drawEnded: false,
    });
  }

  rectangleUpdate(x, y) {
    this.setState({
      right: x,
      bottom: y,
    });
  }

  rectangleEnd() {
    const { onDrawEnd } = this.props;
    const { left, right, top, bottom } = this.state;
    if (onDrawEnd) {
      // Restructure coordinates so top left is closest to origin.
      const l = Math.min(left, right);
      const r = Math.max(left, right);
      const t = Math.min(top, bottom);
      const b = Math.max(top, bottom);

      // Compute props for <rect>
      const width = r - l;
      const height = b - t;
      const x = l;
      const y = t;

      onDrawEnd({
        type: Shape.Type.Rectangle,
        x,
        y,
        width,
        height,
      });
    }

    this.setState({
      left: null,
      right: null,
      top: null,
      bottom: null,
    });
  }

  rectangeDraw() {
    const { fill, stroke } = this.props;
    const { left, right, top, bottom } = this.state;

    if (left && right && top && bottom) {
      // Restructure coordinates so top left is closest to origin.
      const l = Math.min(left, right);
      const r = Math.max(left, right);
      const t = Math.min(top, bottom);
      const b = Math.max(top, bottom);

      // Compute props for <rect>
      const width = r - l;
      const height = b - t;
      const x = l;
      const y = t;

      return (
        <rect
          x={x}
          y={y}
          width={width}
          height={height}
          fill={fill}
          stroke={stroke}
          opacity="0.6"
          style={{ pointerEvents: 'none' }}
        />
      );
    }

    return null;
  }
  //#endregion

  /* Polygon Methods */
  //#region
  polygonStart(x, y) {
    this.setState({
      points: [[x, y]],
      extraPoint: null,
      drawEnded: false,
    });
  }

  polygonAdd(x, y) {
    this.setState(prevState => ({
      points: [...(prevState.points || []), [x, y]],
    }));
  }

  polygonUpdate(x, y) {
    this.setState({
      extraPoint: [x, y],
    });
  }

  polygonEnd() {
    const { onDrawEnd } = this.props;
    const { points } = this.state;
    if (onDrawEnd) {
      onDrawEnd({
        type: Shape.Type.Polygon,
        points,
      });
    }

    this.setState({
      points: null,
      extraPoint: null,
    });
  }

  polygonDraw() {
    const { fill, stroke } = this.props;
    const { points, extraPoint, drawEnded } = this.state;
    if (points) {
      const pts = [...points];
      let startCircle = null;
      if (!drawEnded) {
        if (extraPoint) {
          pts.push(extraPoint);
        }

        const [x, y] = pts[0];
        startCircle = (
          <circle
            key="circle"
            onClick={this.polygonEnd}
            cx={x}
            cy={y}
            r="3"
            style={{ cursor: 'pointer' }}
          />
        );
      }
      return [
        <polygon
          key="polygon"
          points={pts.map(p => p.join(', ')).join(' ')}
          fill={fill}
          fillRule="evenodd"
          stroke={stroke}
          opacity="0.6"
          style={{ pointerEvents: 'none' }}
        />,
        startCircle,
      ];
    }

    return null;
  }
  //#endregion

  /* Freehand Methods */
  //#region
  freehandDraw() {
    const { fill, stroke } = this.props;
    const { points, drawEnded } = this.state;
    if (points) {
      if (drawEnded) {
        return (
          <polygon
            points={points.map(p => p.join(', ')).join(' ')}
            fill={fill}
            fillRule="evenodd"
            stroke={stroke}
            opacity="0.6"
            style={{ pointerEvents: 'none' }}
          />
        );
      } else {
        return (
          <polyline
            points={points.map(p => p.join(', ')).join(' ')}
            fill="none"
            stroke={stroke}
            opacity="0.6"
            style={{ pointerEvents: 'none' }}
          />
        );
      }
    }

    return null;
  }
  //#endregion

  shapeDraw() {
    const { drawMode } = this.props;
    switch (drawMode) {
      case DrawLayer.DrawMode.Circle:
        return this.circleDraw();
      case DrawLayer.DrawMode.Rectangle:
        return this.rectangeDraw();
      case DrawLayer.DrawMode.Polygon:
        return this.polygonDraw();
      case DrawLayer.DrawMode.Freehand:
        return this.freehandDraw();
      default:
        return null;
    }
  }

  render() {
    const { width, height, disabled, drawMode } = this.props;
    const shape = this.shapeDraw();

    return (
      <g>
        <rect
          onMouseDown={this.onMouseDown}
          onMouseUp={this.onMouseUp}
          onMouseMove={this.onMouseMove}
          onClick={this.onClick}
          onKeyDown={this.onKeyDown}
          width={width}
          height={height}
          fill="none"
          style={{
            pointerEvents: disabled ? 'none' : 'all',
            cursor: drawMode ? 'crosshair' : 'auto',
          }}
        />
        {shape}
      </g>
    );
  }
}

// Button that needs to be held to draw.
DrawLayer.ActiveButton = 0; // Left Mouse Button
DrawLayer.HeldActiveButton = 1; // Left Mouse Button

DrawLayer.DrawMode = {
  Circle: 'draw_circle',
  Rectangle: 'draw_rectangle',
  Polygon: 'draw_polygon',
  Freehand: 'draw_freehand',
  EditRectangle: 'edit_rectangle',
  EditPolygon: 'edit_polygon',
};

class Shape extends Component {
  constructor(props) {
    super(props);
    this.state = {
      startX: null,
      startY: null,
    };

    this.normalizeMouse = this.normalizeMouse.bind(this);
    this.onMouseDown = this.onMouseDown.bind(this);
    this.onMouseMove = this.onMouseMove.bind(this);
    this.onMouseUp = this.onMouseUp.bind(this);
  }

  normalizeMouse(target, x, y) {
    const rect = target.getBoundingClientRect();
    return [x - rect.left, y - rect.top];
  }

  onMouseDown(e, _i) {
    const { editable } = this.props;
    if (editable && e.button === Shape.ActiveButton) {
      const [x, y] = this.normalizeMouse(e.target, e.clientX, e.clientY);
      this.setState({
        startX: x,
        startY: y,
      });
    }
  }

  onMouseMove(e) {
    const { editable, onEdit } = this.props;
    if (editable && (e.buttons & Shape.ActiveHeldButton) !== 0) {
      const { startX, startY } = this.state;
      const [x, y] = this.normalizeMouse(e.target, e.clientX, e.clientY);
      if (startX !== null && startY !== null) {
        const dx = x - startX;
        const dy = y - startY;
        onEdit(dx, dy);
      } else {
        this.setState({
          startX: x,
          startY: y,
        });
      }
    }
  }

  onMouseUp() {}

  render() {
    const { type, onEdit, editable, ...shapeProps } = this.props;
    switch (type) {
      case Shape.Type.Circle:
        return <circle vectorEffect="non-scaling-stroke" {...shapeProps} />;
      case Shape.Type.Rectangle:
        return <rect vectorEffect="non-scaling-stroke" {...shapeProps} />;
      case Shape.Type.Polygon:
        const { points, ...polyProps } = shapeProps;
        // let circles = [];
        // if (editable) {
        //   circles = points.map(([x, y], i) => (
        //     <circle
        //       key={i}
        //       cx={x}
        //       cy={y}
        //       r="5"
        //       onMouseDown={e => this.onMouseDown(e, i)}
        //       onMouseMove={e => this.onMouseMove(e, i)}
        //       onMouseUp={e => this.onMouseUp(e, i)}
        //     />
        //   ));
        // }
        return (
          <polygon
            key="polygon"
            points={points.map(p => p.join(', ')).join(' ')}
            fillRule="evenodd"
            vectorEffect="non-scaling-stroke"
            {...polyProps}
          />
        );
      default:
        return null;
    }
  }
}

// Button that needs to be held to edit.
Shape.ActiveButton = 0; // Left Mouse Button
Shape.HeldActiveButton = 1; // Left Mouse Button

Shape.Type = {
  Circle: 'circle',
  Rectangle: 'rectangle',
  Polygon: 'polygon',
};

DrawLayer.Shape = Shape;

export default DrawLayer;
